import moment from 'moment/moment'

export const getAgeByBrithday = (strBirthday) => {
  var returnAge
  var strBirthdayArr = strBirthday.split('-')
  var birthYear = strBirthdayArr[0]
  var birthMonth = strBirthdayArr[1]
  var birthDay = strBirthdayArr[2]
  // var birthD
  var d = new Date()
  var nowYear = d.getFullYear()
  var nowMonth = d.getMonth() + 1
  var nowDay = d.getDate()

  if (nowYear == birthYear) {
    let birthdayBase = this.formatDate(new Date())
    returnAge = (this.DateDiff(birthdayBase, strBirthday) + 1) + '天'
    return returnAge// 返回周岁年龄
  } else {
    var ageDiff = nowYear - birthYear // 年之差
    if (ageDiff > 0) {
      if (nowMonth == birthMonth) {
        var dayDiff = nowDay - birthDay// 日之差
        if (dayDiff < 0) {
          returnAge = (ageDiff - 1) + '岁'
        } else {
          returnAge = ageDiff + '岁'
        }
      } else {
        var monthDiff = nowMonth - birthMonth// 月之差
        if (monthDiff < 0) {
          returnAge = (ageDiff - 1) + '岁'
        } else {
          returnAge = ageDiff + '岁'
        }
      }
    } else {
      returnAge = -1// 返回-1 表示出生日期输入错误 晚于今天
    }
  }
  return returnAge// 返回周岁年龄
}

export const diffDate = (birthday, effectTime) => {
  let diffYears = moment(effectTime).diff(moment(birthday), 'year')
  let diffDays = moment(effectTime).diff(moment(birthday), 'days')
  if (diffYears < 0) return '生效日期小于出生日期!'
  if (diffYears > 0) {
    return diffYears + '岁'
  }
  if (diffYears < 1 && diffYears >= 0) {
    return diffDays + 1 + '天'
  }
}

export const birthByIdNum = (idNum) => {
  let birthday
  if (idNum.length === 15) {
    let idNum15 = this.idNum.substring(0, 6) + '19' + this.idNum.substring(6)
    birthday =
      idNum15.slice(6, 10) +
      '-' +
      idNum15.slice(10, 12) +
      '-' +
      idNum15.slice(12, 14)
  } else {
    birthday =
      idNum.slice(6, 10) +
      '-' +
      idNum.slice(10, 12) +
      '-' +
      idNum.slice(12, 14)
  }
  return birthday
}
