import { uuid } from '@/assets/js/util'
export const getComFormMixin = {
  data () {
    return {
      // succeedShow: false,
      errorTips: '',
      errorIdNum: '',
      errorMobile: '',
      errorPwd: '',
      errorImgCode: '',
      errorMsgCode: '',
      isMessageCode: false,
      imgCodeShow: false,
      isRequesting: false,
      uuid: '',
      // second: 60,
      delImg: {
        'background-image':
        'url(' + require('@/assets/images/del.png') + ')'
      }
    }
  },
  mounted () {
    this.uuid = uuid()
  },
  computed: {
  },
  methods: {
    focus (event, name) {
      this._onFocus(event)
      this.errorTips = ''
      switch (name) {
        case 'mobile':
          this.errorMobile = ''
          break
        case 'idNum':
          this.errorIdNum = ''
          break
        case 'pwd':
          this.errorPwd = ''
          break
        case 'imgCode':
          this.errorImgCode = ''
          break
        case 'msgCode':
          this.errorMsgCode = ''
      }
    },
    del (type) {
      this.sendForm[type] = ''
    },
    _onFocus (event) {
      let children = event.currentTarget.parentElement.children
      for (let i = 0; i < children.length; i++) {
        children[i] !== event.currentTarget && children[i].classList.add('del')
      }
    },
    _onBlur (event) {
      let children = event.currentTarget.parentElement.children
      for (let i = 0; i < children.length; i++) {
        children[i] !== event.currentTarget && children[i].classList.remove('del')
      }
    }
    // blur (event) {
    //   let children = event.currentTarget.parentElement.children
    //   for (let i = 0; i < children.length; i++) {
    //     children[i] !== event.currentTarget && children[i].classList.remove('del')
    //   }
    //   // event.currentTarget.nextElementSibling.getElementsByClassName('del')
    // }
  },
  watch: {
  }

}
