<template>
  <div class="sms-login-container">
    <m-header :rightText="'密码登录'" @btnClick="goPwdLogin"></m-header>
    <form action="" @submit.prevent="submit" class="comForm">
      <div class="input-wrapper">
        <input type="text" v-model.trim="sendForm.idNum" :class="errorIdNum?'active':''" @focus="focus($event,'idNum')"
               @blur="blur($event,'idNum')" placeholder="身份证号">
        <i class="del" v-show="sendForm.idNum" @mousedown.stop="del('idNum')" :style="delImg"></i>
        <span class="error">{{errorIdNum}}</span>
      </div>
      <div class="input-wrapper msgcode-wrapper ">
        <div class="msgCode-layer" :class="errorMsgCode?'errorMsgCode':''">
          <input type="text" v-model.trim="sendForm.msgCode"
                 @focus="focus($event,'msgCode')" @blur="blur($event,'msgCode')" placeholder="短信验证码">
          <i class="del" v-show="sendForm.msgCode" @mousedown.stop="del('msgCode')" :style="delImg"></i>
          <span class="aboutcode" v-if="!isMessageCode" @click="getMessageCode()">发送验证码</span>
          <span class="aboutcode sendcode" v-else>{{second}}s重发</span>
          <span class="error ">{{errorMsgCode}}</span>
        </div>
      </div>
      <div class="submit-wrapper">
        <!--<p class="mobileSendTips" v-if="mobileSendShow">验证码已发送至尾号为{{this.currentMobile}}的手机号</p>-->
        <div class="error-tips" v-show="errorTips">{{errorTips}}</div>
        <input type="submit" v-model="loginBtnText" class="submit" :class="allValid? 'active':''"
               :disabled="!allValid||isRequesting">
      </div>
      <div class=" forget-register-wrapper">
        <router-link tag="div" to="/user/register" class="register-btn">
          还未注册？
          <span class="register-link">立即注册</span>
        </router-link>
      </div>
    </form>
  </div>
</template>

<script>
import mHeader from '@/components/base/mHeader'
import { ValidateUtil } from '@/assets/js/global'
import { idNumErrorTips } from '@/assets/js/errorTips'

import { uuid } from '@/assets/js/util'
import { setLocalStorage, getLocalStorage } from '@/assets/js/localStorage'
import { getComFormMixin } from '@/assets/js/mixins/comForm'
import { mapMutations } from 'vuex'

export default {
  name: 'login',
  mixins: [getComFormMixin],
  data () {
    return {
      mobileSendShow: false,
      loginBtnText: '登录',
      count: 5,
      second: 60,
      currentMobile: '',
      sendForm: {
        idNum: '',
        msgCode: ''
      }
    }
  },
  mounted () {
    this.uuid = uuid()
    this._isCountDown()
  },
  methods: {
    goPwdLogin () {
      this.$router.push('/user/pwdLogin')
    },
    submit () {
      this.loginBtnText = '登录中....'
      this.isRequesting = true
      let postData = {
        idNum: this.sendForm.idNum,
        hiddenCaptcha: this.uuid,
        captcha: this.sendForm.msgCode
      }
      this.$api.user.smsLogin(postData).then((res) => {
        this.isRequesting = false
        this.loginBtnText = '登录'
        if (res.code === 1) {
          this.SET_TOKEN(res.data.csrfToken)
          this.SET_USER_INFO(res.data.member)
          if (this.$route.params.redirctUrl) {
            this.$router.push({
              path: `/37duc/product/detail/${this.$route.params.productId}`,
              query: {
                productId: this.$route.params.productId
              }
            })
          } else {
            this.$router.push('/personalCenter')
          }
        } else {
          if (res.message.includes('短信验证码错误')) {
            this.errorMsgCode = res.message
          } else {
            this.errorTips = res.message
          }
        }
      })
    },
    getMessageCode () {
      if (!this.sendForm.idNum) {
        this.errorIdNum = idNumErrorTips.notNull
        return
      }
      if (this.errorIdNum) return
      let postData = {
        idNum: this.sendForm.idNum,
        captchaId: this.uuid
      }
      this.$api.user.sendCaptchaSms(postData).then((res) => {
        if (res.code === 1) {
          // this.mobileSendShow = true
          this.currentMobile = res.data.mobile.substr(res.data.mobile.length - 4)
          this.$toast.center(`验证码已发送尾数为${this.currentMobile}的手机号`)
          this.readsecond()
          this.setMobileSendTime()
        }
        if (res.code === 0) {
          this.errorIdNum = res.message
        }
      })
    },
    blur (event, name) {
      this._onBlur(event)
      switch (name) {
        case 'idNum':
          this._validIdNum()
          break
        default:
      }
    },
    setMobileSendTime () {
      let timer = setInterval(() => {
        if (this.count <= 1) {
          this.mobileSendShow = false
          this.count = 5
          clearInterval(timer)
          return
        }
        this.count--
      }, 1000)
    },
    readsecond () {
      this.isMessageCode = true
      var timer = setInterval(() => {
        if (this.second <= 1) {
          this.isMessageCode = false
          this.second = 60
          clearInterval(timer)
          return
        }
        this.second--
        setLocalStorage('smsSecond', this.second)
      }, 1000)
    },
    _isCountDown () {
      let sec = getLocalStorage('smsSecond')
      if (sec > 1) {
        this.second = sec
        this.isMessageCode = true
        this.readsecond()
      }
    },
    _validIdNum () {
      if (this.sendForm.idNum) {
        if (!ValidateUtil.idCardRegexs(this.sendForm.idNum).pass) {
          this.errorIdNum = ValidateUtil.idCardRegexs(this.sendForm.idNum).tip
        }
      }
    },
    ...mapMutations([
      'SET_TOKEN',
      'SET_USER_INFO'
    ])
  },
  computed: {
    allValid () {
      return this.sendForm.idNum && this.sendForm.msgCode && !this.errorIdNum
    }
  },
  components: { mHeader }

}
</script>
<style scoped lang="scss">
  @import "@/assets/styles/comForm.scss";

  .sms-login-container {
    background: #fff;
    position: fixed;
    top: pxToRem(84px);
    left:0;
    width:100%;
    overflow-y: scroll;
    min-height: 100%;
    z-index: 103;
    .comForm {
      .submit-wrapper {
        margin-top: .64rem;
      }
    }
  }

</style>
